import React from "react";
import { v4 } from "uuid";

// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// components
import WhiteCard from "components/shared/WhiteCard/WhiteCard";

// needed styles
import styles from "./index.module.scss";

const ProgramDetailsTable = ({ singleTrip }) => {
  const {
    start_flight_date,

    airline_ar,
    airline_en,
    desc_ar,
    desc_en,
    duration_ar,
    duration_en,
    end_flight_date,
    hotel_ar,
    hotel_en,
    madina_night,
    means_of_travel_ar,
    means_of_travel_en,
    mecca_night,
  } = singleTrip || {};
  const tableData = [
    {
      header: "تاريخ الرحلة",
      body: `من ${start_flight_date}    الي ${end_flight_date}`,
      // body: {
      //   to: "22/7/2022",
      //   from: "22/7/2022",
      // },
    },
    {
      header: "شركة الطيران",
      body: `${airline_ar}`,
    },
    {
      header: "وسيلة السفر",
      body: `${means_of_travel_ar}`,
    },
    {
      header: "  مدة الرحلة ",
      body: `${duration_ar}`,
    },
    {
      header: "ليالي المدينة",

      body: `${madina_night}`,
    },
    {
      header: "ليالي مكة",
      body: `${mecca_night}`,
    },
  ];

  return (
    <WhiteCard>
      <h3>البرنامج :</h3>

      <div className={styles["table"]}>
        <div className={styles["table-header"]}>العمره السياحيه</div>
        <div className={styles["table-body"]}>
          <table>
            {tableData?.map((tableItem) => (
              <tr key={v4()}>
                <th>{tableItem?.header}</th>
                <td>{tableItem?.body}</td>
                {/* {typeof tableItem?.body === "object" ? (
                  <>{renderTableObject(tableItem?.body)}</>
                ) : (
                  <>
                  </>
                )} */}
              </tr>
            ))}
          </table>
        </div>
      </div>
    </WhiteCard>
  );
};

export default ProgramDetailsTable;
