import React from "react";
import { v4 } from "uuid";
// components
import WhiteCard from "components/shared/WhiteCard/WhiteCard";

// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
import styles from "./index.module.scss";

const ProgramDetails = ({
  header = "السعر يشمل",
  rightIcon,
  wrongIcon,
  data,
}) => {
  const renderProperIcon = rightIcon ? (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="16"
      height="18.555"
      viewBox="0 0 24.231 18.555"
    >
      <path
        id="Icon_ionic-ios-checkmark"
        data-name="Icon ionic-ios-checkmark"
        d="M34.537,14.378l-1.98-2.037a.426.426,0,0,0-.315-.135h0a.409.409,0,0,0-.315.135L18.2,26.17l-5-5a.435.435,0,0,0-.63,0l-2,2a.448.448,0,0,0,0,.641l6.3,6.3a1.993,1.993,0,0,0,1.317.641,2.088,2.088,0,0,0,1.305-.619H19.5L34.548,15.019A.481.481,0,0,0,34.537,14.378Z"
        transform="translate(-10.434 -12.206)"
        fill="#00c253"
      />
    </svg>
  ) : wrongIcon ? (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="16"
      height="23.657"
      viewBox="0 0 23.657 23.657"
    >
      <g
        id="Group_210"
        data-name="Group 210"
        transform="translate(-1297.672 -3227.672)"
      >
        <line
          id="Line_65"
          data-name="Line 65"
          x2="18"
          y2="18"
          transform="translate(1300.5 3230.5)"
          fill="none"
          stroke="red"
          strokeLinecap="round"
          strokeWidth="4"
        />
        <line
          id="Line_66"
          data-name="Line 66"
          x1="18"
          y2="18"
          transform="translate(1300.5 3230.5)"
          fill="none"
          stroke="red"
          strokeLinecap="round"
          strokeWidth="4"
        />
      </g>
    </svg>
  ) : null;

  // const data = [
  //   "الإنتقالات بأحدث الاتوبيسات السياحية المكيفة",
  //   "الإنتقالات بأحدث الاتوبيسات السياحية المكيفة",
  //   "الإنتقالات بأحدث الاتوبيسات السياحية المكيفة",
  //   "الإنتقالات بأحدث الاتوبيسات السياحية المكيفة",
  // ];

  return (
    <WhiteCard>
      <h3 className={styles["header"]}>{header} :</h3>
      <div>
        <ul className={styles["container"]}>
          {data?.map((item) => (
            <div key={v4()} className={styles["infoWithSvg"]}>
              {renderProperIcon}
              <li className={styles["info"]}>{item}</li>
            </div>
          ))}
        </ul>
      </div>
    </WhiteCard>
  );
};

export default ProgramDetails;
